export const shareJob = async (id,title,text) => {
    const dataToShare = {
        title: title,
        text: text,
        url: window.location.origin + '/jobs/' + id
    }
    if (navigator.share) {
        try {
            await navigator.share(dataToShare);
        } catch (error) {
            console.warn(error);
        }
    }
}