import React, { Fragment } from "react";
import Card from "react-bootstrap/Card";
import Col from "react-bootstrap/Col"
import Row from "react-bootstrap/Row"
import { useNavigate } from "react-router-dom";
import { shareJob } from "../../common/shareJob";

import styles from "./JobItem.module.css"

export default function JobItem({ item }) {
    let navigate = useNavigate();

    return (
        <Fragment>
            <Col sm={6}  md={4} >
                <Card className={styles.cardwidth}>
                    <Card.Body>
                        <Row>
                            <Col md={3}>
                                <Card.Link onClick={() => navigate('/recruiter/jobs/' + item.email)}><Card.Img variant="top" src="https://via.placeholder.com/25x25" /></Card.Link>
                            </Col>
                            <Col className="d-flex justify-content-between" md={9}>
                                <div>
                                    <Card.Title className={styles.error} >{item.title ? item.title : "Software Developer"}</Card.Title>
                                    <Card.Subtitle className="mb-2 text-muted">{item.jobLocation ? item.jobLocation : "USA"}</Card.Subtitle>
                                    <Card.Text>
                                        {item.jobType ?? "Remote"}
                                    </Card.Text>
                                </div>
                                <div className="d-flex flex-column justify-content-around">
                                    <a href="#save" onClick={() => alert("Bookmark me")}><i className="bi bi-heart"></i></a>
                                    <a href="#save" onClick={() => shareJob(item.email, "www.elihr.com", "Check this job out, you may like it")}><i className="bi bi-share d-xs-block  d-md-none"></i></a>
                                    {/* <a href="#edit" onClick={() => navigate('/recruiter/jobs/' + item.email + '/edit')}><i className="bi bi-pencil-square"></i></a> */}
                                </div>
                            </Col>
                        </Row>
                    </Card.Body>
                </Card>
            </Col>
        </Fragment>
    )
}